.home-container {
  background-color: #222a31;
  margin: 0;
  height: 100%;
  min-height: 100vh;
  width: 100%;
  min-width: 97vw;
}

.section-container {
  display: flex;
}

.movies-container {
  height: 100%;
  width: 90%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  padding-left: 40px;
  margin-top: 40px;
}

.cont{
  display: flex;
  flex-direction: column;
}

.cont > h1{
  margin-left: 40px;
  font-family: 'Roboto', sans-serif;
  font-weight:300;
  color: #B4B4B4;

}

@media screen and (max-width: 800px) {
  .section-container{
    display: flex;
    flex-direction: column;
    color: red;
    
  }
  .nav-bar{
    width: 100%;
   
  }
  
}

